import React, { useEffect, useState } from 'react';
import { Formik, Form, Field } from 'formik';
import { useHistory } from 'react-router-dom';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';

// material-ui
import { makeStyles } from '@material-ui/styles';
import { Button, Grid, IconButton, InputAdornment, useMediaQuery, MenuItem } from '@material-ui/core';

// project imports
import useScriptRef from '../../../hooks/useScriptRef';
import { strengthColor, strengthIndicator } from '../../../utils/password-strength';

// assets
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { FormProvider, RHFTextField, RHFSelect, RHFCheckbox } from '../../../components/hook-form';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { registerUserByEmail } from '../../../store/actions/AuthActions/Register.actions';
import { getSchoolList } from '../../../store/actions/SchoolActions';
import { setMessage } from '../../../store/actions/MessageActions';

// style constant
const useStyles = makeStyles((theme) => ({
    loginInput: {
        ...theme.typography.customInput
    },
    schoolList: {
        marginTop: 8,
        marginBottom: 8,
        '& > label': {
            top: '10px',
            left: 0,
            color: theme.grey500,
            '&[data-shrink="false"]': {
                top: '5px'
            }
        },
        '& > div > input': {
            padding: '30.5px 14px 11.5px !important'
        },
        '& legend': {
            display: 'none'
        },
        '& fieldset': {
            top: 0
        }
    },
    em: {
        fontStyle: 'normal',
        color: '#D3D3D3'
    }
}));

// const names = ['Mother Teresa', 'Mother Teresa', 'Mother Teresa', 'Mother Teresa', 'Mother Teresa', 'Mother Teresa'];

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250
        }
    }
};
const FormSchema = Yup.object().shape({
    termsAndConditions: Yup.bool().oneOf([true], <span style={{ color: 'red' }}> Please accept the terms and conditions </span>)
});
const FormComponent = ({ setIsTermsAccepted }) => {
    

    return (
        <Formik
            initialValues={{
                termsAndConditions: false
            }}
            validationSchema={FormSchema}
            onSubmit={(values) => {
                console.log(values);
            }}
        >
            {({ errors, setFieldValue }) => (
                <Form>
                    <div>
                        {errors.termsAndConditions && <p>{errors.termsAndConditions}</p>}
                        &nbsp;
                        <label>
                            <Field
                                type="checkbox"
                                name="termsAndConditions"
                                onChange={(e) => {
                                    setFieldValue('termsAndConditions', e.target.checked);
        
                                    setIsTermsAccepted(e.target.checked);
                                }}
                            />
                            &nbsp;
                            <a href="/termsConditions" target="_blank">
                                Terms and conditions
                            </a>
                        </label>
                    </div>
                </Form>
            )}
        </Formik>
    );
};

const RestRegister = ({ ...others }) => {
    const classes = useStyles();
    let history = useHistory();
    const dispatch = useDispatch();
    const scriptedRef = useScriptRef();
    const matchDownSM = useMediaQuery((theme) => theme.breakpoints.down('sm'));
    const [showPassword, setShowPassword] = React.useState(false);
    const [showConfirmPassword, setShowConfirmPassword] = React.useState(false);

    const [strength, setStrength] = useState(0);
    const [level, setLevel] = useState('');
    const [schoolName, setSchoolName] = useState('');
    const { schoolList } = useSelector((state) => state?.school);
    const [isProcessing, setIsProcessing] = useState(false);
    const [isTermsAccepted, setIsTermsAccepted] = useState(false);


    const RegisterSchema = Yup.object().shape({
        firstName: Yup.string().required('First name is required'),
        lastName: Yup.string().required('Last name is required'),
        email: Yup.string().email('Email must be a valid email address').required('Email is required'),
        password: Yup.string().required('Password is required'),
        dob: Yup.string('Enter Date Of Birth').trim().required('Date Of Birth is required'),
        confirmPassword: Yup.string()
            .oneOf([Yup.ref('password'), null], 'Password must match')
            .required('Confirm Your Password'),
        mobile: Yup.string().required('Mobile is required'),
        //terms: Yup.boolean().required('You need to check this, in order to proceed')
        termsAndConditions: Yup.bool().required('Please accept the terms and conditions').oneOf([true])
    });

    let defaultValues = {
        email: '',
        password: '',
        mobile: '',
        firstName: '',
        lastName: '',
        schoolId: '',
        classAndSection: '',
        dob: '',
        termsAndConditions: true
    };

    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(RegisterSchema),
        defaultValues
    });

    const { handleSubmit, watch } = methods;
    const hide = watch('schoolId');

    const handleSchoolChange = (event) => {
        setSchoolName(event?.target?.value);
    };

    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };
    const handleClickShowConfirmPassword = () => {
        setShowConfirmPassword(!showConfirmPassword);
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    const changePassword = (value) => {
        const temp = strengthIndicator(value);
        setStrength(temp);
        setLevel(strengthColor(temp));
    };

    const onSubmit = async (data) => {
        setIsProcessing(true)
        const payload = {
            firstName: data?.firstName,
            lastName: data?.lastName,
            email: data?.email,
            password: data?.password,
            mobile: data?.mobile,
            name: data?.name,
            schoolId: data?.schoolId,
            classAndSection: data?.classAndSection
        };
        //setDisabled(true);

        let modifiedPayload = {};
        if (data?.schoolId === '') {
            modifiedPayload = {
                firstName: data?.firstName,
                lastName: data?.lastName,
                email: data?.email,
                password: data?.password,
                mobile: data?.mobile,
                name: data?.name,
                termsAndConditions: data?.termsAndConditions,
                dob: data?.dob
            };
        } else {
            modifiedPayload = {
                firstName: data?.firstName,
                lastName: data?.lastName,
                email: data?.email,
                password: data?.password,
                mobile: data?.mobile,
                name: data?.name,
                schoolId: data?.schoolId,
                classAndSection: data?.classAndSection,
                termsAndConditions: data?.termsAndConditions,
                dob: data?.dob
            };
        }

        try {
            const res = await dispatch(registerUserByEmail(modifiedPayload, () => {
                history.push('/login');
            }));
            dispatch(setMessage(res?.message));
        } catch (error) {
            console.log('Error:', error);
            setIsProcessing(false);
        }
    };

    useEffect(() => {
        changePassword('123456');
        dispatch(getSchoolList());
    }, []);

    return (
        <>
            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                <Grid container spacing={matchDownSM ? 0 : 2}>
                    <Grid item xs={12}>
                        <RHFTextField name="firstName" placeholder="Enter your first name" autoFocus />
                    </Grid>
                    <Grid item xs={12}>
                        <RHFTextField name="lastName" placeholder="Enter your last name" />
                    </Grid>
                    <Grid item xs={12}>
                        <RHFTextField name="mobile" placeholder="Enter your phone" />
                    </Grid>
                    <Grid item xs={12}>
                        <RHFSelect name="schoolId" label="">
                            <option value="" selected>
                                Select a School (For Library user - School Delivery)
                            </option>

                            {schoolList?.map((item, index) => (
                                <>
                                    <option key={index} value={item?.id}>
                                        {item?.nameBranch}
                                    </option>
                                </>
                            ))}
                        </RHFSelect>
                    </Grid>

                    {hide !== '' && hide !== 'Select a School' && (
                        <Grid item xs={12}>
                            <RHFTextField name="classAndSection" placeholder="Enter your class/section" />
                        </Grid>
                    )}
                    <Grid container spacing={1} marginTop={0.5}>
                        <Grid item marginLeft={2} marginTop={0.8} style={{ opacity: 0.5 }}> Date of Birth </Grid>
                        <Grid item marginLeft={3} xs={8}>
                            <RHFTextField type="date"  name="dob" />
                        </Grid>
                    </Grid>

                    <Grid item xs={12}>
                        <RHFTextField name="email" placeholder="Enter your email" />
                    </Grid>
                    <Grid item xs={12}>
                        <RHFTextField
                            className={classes.loginInput}
                            name="password"
                            placeholder="Password"
                            type={showPassword ? 'text' : 'password'}
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton onClick={() => setShowPassword(!showPassword)} edge="end">
                                            {showPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <RHFTextField
                            className={classes.loginInput}
                            name="confirmPassword"
                            placeholder="Confirm password"
                            type={showConfirmPassword ? 'text' : 'password'}
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton onClick={() => setShowConfirmPassword(!showConfirmPassword)} edge="end">
                                            {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <FormComponent setIsTermsAccepted={setIsTermsAccepted} />
                    </Grid>
                    <Grid item xs={12}>
                        <Button
                            disableElevation
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                            disabled={isProcessing || !isTermsAccepted} // Disable if terms are not accepted
                        >
                            {isProcessing ? "Processing..." : "Sign Up"}
                        </Button>
                    </Grid>
                </Grid>
            </FormProvider>
        </>
    );
};

export default RestRegister;
